<template>
  <div style="margin: 20px">
    <Steps :current="step">
      <Step title="注册小程序" content="通过微信公众平台注册认证小程序"></Step>
      <Step title="授权小程序" content="同意并授权小程序到管理后台"></Step>
      <Step title="完成授权" content="完成授权后代小程序实现业务"></Step>
    </Steps>
    <!-- <Button type='primary' @click='handleAuth'>一键授权</Button> -->
    <div style="margin-top: 60px;"></div>
    <Form ref="formValidate" :model="wxaconfig" :rules="ruleValidate" label-position="left" :label-width="120">
      <FormItem label="Appid" prop="appid">
        <Input v-model="wxaconfig.appid" placeholder="请输入小程序appid！" size="large" ></Input>
      </FormItem>
      <FormItem label="App Secret" prop="appsecret">
        <Input v-model="wxaconfig.appsecret" placeholder="请输入对应appsecret！" size="large" type='password'></Input>
      </FormItem>
      <FormItem>
        <Button type="primary" :loading="loading" @click="handleSubmit('formValidate')" style="width:100px;">
          <span v-if="!loading">提交</span>
          <span v-else>正在保存..</span>
        </Button>
        <Button type="ghost" @click="handleReset('formValidate')" size="large" style="margin-left: 8px;width:80px">取消</Button>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      step: 1,
      loading: false,
      wxaconfig: {
        appid: '',
        appsecret: ''
      },
      ruleValidate: {
        appid: [
          { required: true, message: '请输入小程序appid！', trigger: 'blur' }
        ],
        appsecret: [
          { required: true, message: '请输入appid对应appsecret！', trigger: 'blur' }
        ]
      }
    }
  },
  mounted: function() {
    this.$sitemap.prvname = "小程序管理";
    this.$sitemap.currname = '小程序参数配置';
    this.$menuactive.open = ['SystemBlock'];
    this.$menuactive.active = 'WxaConfig';
    this.getWxaAuthstate()
  },
  methods: {
    handleSubmit(name) {
      var thatform = this.$refs[name];
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.loading = true;
          this.wxaConfigSetting(thatform);
        } else {
          this.$Message.error('还没有完整的信息输入！');
        }
      })
    },
    handleReset(name) {
      this.$refs[name].resetFields();
    },
    getWxaAuthstate() {
      this.$http.post(this.$api.wxa_authstate, {}).then(res => {
        console.log('获取维系小程序绑定信息', res.data)
        if (res.data.data) {
          this.step = 2;
          this.wxaconfig.appid = res.data.data.appid;
          this.wxaconfig.appsecret = res.data.data.appsecret;
        } else {
          this.step = 1;
        }
      }).catch(error => {
        this.$Message.error(error)
      })
    },
    wxaConfigSetting: function(thatform) {
      var that = this;
      this.$http.post(this.$api.wxa_save, that.wxaconfig).then(function(res) {
        that.loading = false;
        if (res.data.result == '1') {
          thatform.resetFields();
          that.step = 2;
          that.$Message.success('小程序参数配置成功!');
        } else {
          that.$Message.error('操作失败，请检查原因！');
        }
      });
    },
    handleAuth() {
      this.$http.post(this.$api.wxa_auth,{}).then(res=>{
        window.location.href = res.data.data
      }).catch(error=>{
        this.$Message.error(error)
      })
    },
  }
}

</script>



